import React from 'react'
import Title from '../common/title/Title'
import Header from '../common/header/Header'
import WhatWeDoItem from './WhatWeDoItem'
import whatWeDoState from './whatWeDoState'
import WhatWeDoElem from './WhatWeDoElem'
import { ThemeContext } from './../../App'
import { titleState } from '../common/title/titleState'
import SubTitle from '../common/subTitle/SubTitle'
import { subTitleState } from './../common/subTitle/subTitleState'
import styled from 'styled-components'
import whatWeDoImg from './../../assets/h1-background-img-2.jpg'

const WhatWeDoStyle = styled.div`
  
`
const WhatWeDoPage = (props) => {
  console.log(props)
  return (
    <WhatWeDoStyle >
    <ThemeContext.Consumer> 
        {themeColors => <Header value={themeColors.dark} navItems={props.navItems}/> } 
      </ThemeContext.Consumer>
      
      <Title dark title={titleState[1]} />
      <WhatWeDoItem whatWeDoItem={whatWeDoState.whatWeDoItem}
        subTitleState={subTitleState[1]}
        background={whatWeDoImg} 
      />
      <WhatWeDoElem whatWeDoElem={whatWeDoState.whatWeDoElem}/>
      <SubTitle subTitleState={subTitleState[7]} />

    </WhatWeDoStyle>
  )
}
export default WhatWeDoPage