import React from 'react'
import styled from 'styled-components'

const MenuItemsStyle = styled.div`
    width: 80%;
    display: flex;;
    padding: 30px 10px;
    color: #fff;
     
    .menu-content {
        width: 100%;
    }

    .menu-title {
        margin: 10px;
    }
    
    .menu-description {
        display: flex;
        color: #F9F9F9 !important; 
        padding: 0 15px;
        display: flex;  
    }
    .menu-text {
        font-size: 13px;
    }
    .menu-dots {
        border-bottom: 1px dotted; 
        width: 35%;
        margin: 7px 15px;
    }
`;

const MenuItem = (props) => {
    return (
        <MenuItemsStyle>
            <img className='menu-img' src={props.src} alt={props.title}/>
            <div className='menu-content'>
                <h3 className='menu-title'>{props.title}</h3>
                <div className='menu-description'>
                    <p className='menu-text'>{props.description}</p>
                    <div className='menu-dots'></div>
                    <div className='menu-price'>{props.price}</div>
                </div>
            </div>
        </MenuItemsStyle>
    )
}
export default MenuItem