import React from "react";
import styled, { css } from "styled-components";

const TitleStyle = styled.div`
  position: relative;
  top: -90px;
  background: url(${(props) => props.title.src}) fixed;
  height: 678px;
  width: 100%;
`;
const TitleBody = styled.div`
  width: 40%;
  color: #fff;
  position: absolute;
  top: 40%;
  left: 15%;
  text-align: center;
  cursor: pointer;

  ${(props) =>
    props.light &&
    css`
      color: ${(props) => props.color || "#fff"};
      text-align: ${(props) => props.textAlign || "left"};
    `}

  ${(props) =>
    props.dark &&
    css`
      color: ${(props) => props.color || "#030303"};
    `}
`;

const TitleBodyTitle = styled.h1`
  padding-bottom: 20px;
`;

const TitleBodyText = styled.p`
  padding-top: 20px;
  font-size: 1.25rem;
`;

const Title = (props) => {
  console.log(props);
  return (
    <TitleStyle {...props}>
      <TitleBody {...props}>
        <TitleBodyTitle>{props.title.title}</TitleBodyTitle>
        <hr />
        <TitleBodyText>{props.title.text} </TitleBodyText>
      </TitleBody>
    </TitleStyle>
  );
};
export default Title;
