import React, { useState, useEffect } from 'react'
import styled from 'styled-components'

import SubTitle from '../../common/subTitle/SubTitle'

import offerImg1 from './../../../assets/h1-img-15.png'

const CarouselOfferStyle = styled.div`
  position: relative;
  margin: auto;
  overflow: hidden;
  

  .slide {
    padding-bottom: 150px;
  }

  .carousel-offer__img {
    width: 40%;
  }

  .carousel-offer__body {
    width: 50%;
    margin: 0 auto;
    text-align: center;
    line-height: 1.5rem;
  }

  .carousel-text {
    font-size: 17px;
  }

  .carousel-author {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 25px;;
  }

  .carousel-offer__body_sign {
    padding: 30px 0;
    height: auto;
}
  .active {
    display: flex;
  }

  .inactive {
    display: none;
  }
`
const CarouselOffer = (props) => {

  const len = props.carouselItem.length - 1
  const [activeIndex, setActiveIndex] = useState(0)
 
  useEffect(() => {
    const interval = setInterval(() => {
      setActiveIndex(activeIndex === len ? 0 : activeIndex + 1)
    }, 3000)
    return () => clearInterval(interval)
  }, [activeIndex])

  return (
    <CarouselOfferStyle>
      {props.carouselItem.map((slide, index) => (
        <div key={index} className={index === activeIndex ? 'slide-i active' : 'inactive'}>
          <img className='carousel-offer__img' src={slide.photo} />
          <div className='carousel-offer__body'>
            <SubTitle subTitleState={props.subTitleState[6]} />
            <img className='carousel-offer__body_sign' src={offerImg1} />
          </div>

        </div>
      )
      )}
    </CarouselOfferStyle>
  )
}
export default CarouselOffer
