import React from 'react'
import Timeline from '../../common/timeline/Timeline'
import styled from 'styled-components'
import CarouselOffer from '../../common/carousel/CarouselOffer'

const OfferStyle = styled.div`
    width: 80%;
    margin: 0 auto;
`


const Offer = (props) => {
    return (
        <OfferStyle>
            <CarouselOffer carouselItem={props.carouselItem} subTitleState={props.subTitleState} />
            <Timeline carouselItem={props.carouselItem}/>
        </OfferStyle>
    )
}
export default Offer
