import React, { useState } from "react";
import { Link } from "react-router-dom";
import { IoCartOutline } from "react-icons/io5";
import { BsSearch } from "react-icons/bs";
import styled from "styled-components";

const HeaderStyle = styled.div`
  position: relative;
  z-index: 100;
  background: transparent scroll;
  height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;

  .navbar-logo img {
    width: 120px;
    height: 60px;
  }
  .navbar {
    display: grid;
    grid-template-columns: repeat(10, auto);
    justify-content: end;
    list-style: none;
    width: 80%;
  }

  .nav-links {
    cursor: pointer;
    text-decoration: none;
    position: relative;
    line-height: 1;
    font-size: 0.875rem;
    margin: 1.25rem;
    color: ${(props) => props.value.color};
  }

  .nav-links:after {
    display: block;
    position: absolute;
    left: 0;
    width: 0;
    height: 1px;
    background-color: ${(props) => props.value.backgroungColor};
    content: "";
    transition: width 0.2s ease-out;
  }

  .nav-links:hover:after,
  .nav-links:focus:after {
    width: 100%;
  }

  .nav-links-mobile {
    display: none;
  }

  .menu-icon {
    display: none;
  }
  .tooltip {
    position: absolute;
    top: 25px;
    right: 5px;
    width: 250px;
    padding: 25px;
    background-color: #fff;
    border: 1px solid #efeeec;
    font-size: 0.9rem;
  }
  .info-number {
    position: absolute;
    top: -0.5rem;
    background-color: #c9a74d;
    width: 13px;
    height: 13px;
    border-radius: 100%;
    right: -1.65rem;
    font-size: 10px;
    line-height: 13px;
    text-align: center;
    color: #fff;
  }

  @media screen and (max-width: 768px) {
    header {
      width: 100%;
      position: fixed;
      background-color: #fff;
      border-bottom: 1px solid #f5f4f2;
    }

    .nav-logo {
      display: block;
      justify-content: center;
      align-items: center;
      background-color: #fff;
    }

    .navbar {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.8s ease;
      text-transform: uppercase;
      background-color: #fff;
    }

    .navbar.active {
      color: #c9a74d;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }

    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }

    .nav-links:hover:after,
    .nav-links:focus:after {
      display: none;
    }

    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }

    .nav-links-mobile {
      display: block;
      text-align: center;
      padding: 1.5rem;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      text-decoration: none;
      color: #c9a74d;
      font-size: 1.5rem;
    }

    .nav-links-mobile:hover {
      background: #fff;
      color: #c9a74d;
      transition: 250ms;
    }

    button {
      height: auto;
      display: none;
    }
  }
`;

const Header = (props) => {
  const [click, setClick] = useState(false);
  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);
  const [isShown, setIsShown] = useState(false);

  return (
    <HeaderStyle {...props}>
      <Link to="/" className="navbar-logo" onClick={closeMobileMenu}>
        <img src={props.value.logo} alt="Logo" />
      </Link>
      <div className="menu-icon" onClick={handleClick}>
        <i className={click ? "fas fa-times" : "fas fa-bars"} />
      </div>
      <ul className={click ? "navbar active" : "navbar"}>
        <li className="nav-item">
          <Link to="/about-us" className="nav-links" onClick={closeMobileMenu}>
            About Us
          </Link>
        </li>
        <li className="nav-item">
          <Link
            to="/what-we-do"
            className="nav-links"
            onClick={closeMobileMenu}
          >
            What We Do
          </Link>
        </li>
        <li className="nav-item">
          <Link to="/our-team" className="nav-links" onClick={closeMobileMenu}>
            Our Team
          </Link>
        </li>
        <li className="nav-item">
          <Link
            to="/our-process"
            className="nav-links"
            onClick={closeMobileMenu}
          >
            Our process
          </Link>
        </li>
        <li className="nav-item">
          <Link
            to="/pricing-plans"
            className="nav-links"
            onClick={closeMobileMenu}
          >
            Pricing Plans
          </Link>
        </li>
        <li className="nav-item">
          <Link
            to="/contact-us"
            className="nav-links"
            onClick={closeMobileMenu}
          >
            Contact Us
          </Link>
        </li>
        <li className="nav-item">
          <Link to="/blog" className="nav-links" onClick={closeMobileMenu}>
            Blog
          </Link>
        </li>
        <li className="nav-item">
          <Link to="/shop" className="nav-links" onClick={closeMobileMenu}>
            Shop
          </Link>
        </li>
        <li className="nav-item">
          <Link to="/search" className="nav-links" onClick={closeMobileMenu}>
            <BsSearch style={{ fontSize: 18 }} />
          </Link>
        </li>
        <li className="nav-item">
          <Link
            to="/cart"
            className="nav-links"
            onClick={closeMobileMenu}
            onMouseEnter={() => setIsShown(true)}
            onMouseLeave={() => setIsShown(false)}
          >
            <IoCartOutline style={{ fontSize: 20 }} />
            <div className="info-number">0</div>
            <div>
              {isShown && (
                <div className="tooltip">No products in the cart.</div>
              )}
            </div>
          </Link>
        </li>
      </ul>
    </HeaderStyle>
  );
};
export default Header;
