import React from "react";
import { ThemeContext } from "./../../App";
import styled from "styled-components";

import { subTitleState } from "./../common/subTitle/subTitleState";
import { homeState } from "./homeState";
import Slider from "../common/slider/Slider";
import AmazingTaste from "./amazingTaste/AmazingTaste";
import Assortment from "../common/assortment/Assortment";
import Tradition from "./tradition/Tradition";
import Flavor from "./flavor/Flavor";
import Special from "./special/Special";
import Menu from "./menu/Menu";
import Team from "./team/Team";
import Offer from "./offer/Offer";
import Header from "../common/header/Header";

const HomePageStyle = styled.div``;
const HomePage = (props) => {
  console.log(props);
  return (
    <HomePageStyle>
      <ThemeContext.Consumer>
        {(themeColors) => <Header value={themeColors.dark} />}
      </ThemeContext.Consumer>
      <Slider slider={homeState.sliderItem} />
      <Tradition
        traditionItem={homeState.traditionItem}
        subTitleState={subTitleState}
      />
      <Assortment assortmentItem={homeState.assortmentItem} />
      <AmazingTaste
        subTitleState={subTitleState}
        carouselItem={homeState.amazingItem}
      />
      <Flavor subTitleState={subTitleState} />
      <Special subTitleState={subTitleState} />
      <Menu subTitleState={subTitleState} menuItem={homeState.menuItem} />
      <Team subTitleState={subTitleState} teamItem={homeState.teamItem} />
      <Offer subTitleState={subTitleState} carouselItem={homeState.offerItem} />
    </HomePageStyle>
  );
};
export default HomePage;
