import React from 'react'
import special from './../../../assets/h1-img-19.png'
import SubTitle from '../../common/subTitle/SubTitle'
import Button from '../../common/button/Button'
import styled from 'styled-components'

const FlavorStyle = styled.div`
    display: flex;
    height: auto;
    width: 100%;
    .special-img {
        width: 40%;
        position: relative;
        top: -90px;
    }
`

const Special = (props) => {
    
    return (
        <FlavorStyle>
            <div className='special-body'>
                <SubTitle subTitleState={props.subTitleState[3]} />
            </div>
            <img className='special-img' src={special} alt='slide-img' />
        </FlavorStyle>
    )
}
export default Special
