import React from 'react';
import SubTitle from './../../common/subTitle/SubTitle'
import menuBg from './../../../assets/h1-parallax-2.jpg'
import styled from 'styled-components'
import MenuItems from './MenuItem'

const MenuStyle = styled.div`
    background: url(${menuBg}) fixed;
    z-index: -2;
    position: relative;
    top: -150px;
    color: #fff;
    

    .menu-body {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        width: 80%;
        margin: 0 auto;
    }
`

const Menu = (props) => {
    let state = props.menuItem
    let menuItems = state.map(i => <MenuItems key={i.id} title={i.title} description={i.description} src={i.src} price={i.price} />)

    return (
        <MenuStyle>
            <SubTitle subTitleState={props.subTitleState[4]} />
            <div className='menu-body'>
                {menuItems}
            </div>
        </MenuStyle>
    )
}
export default Menu