import React from 'react';
import './Footer.css'
import { footer } from './footerData'

import imgFooter1 from './../../../assets/footer-image-2.png';
import imgFooter2 from './../../../assets/footer-img-2.png';


const Footer = (props) => {
    return (
        <footer>
            <div className='footer-content'>
                <div className='footer-content__element footer-content__element_contacts'>
                    <h3 className='footer-title'>Sweet Tooth</h3>
                    <hr className='footer-separator' />
                    <p className='footer-text'>
                        Eight Avenue 487, New York <br />
                        +387643932728 <br />
                        pastery@sweettooth.com <br />
                        Elated Themes
                    </p>
                </div>
                <div className='footer-content__element footer-content__element_about'>
                    <img className='footer-img' src={imgFooter1} style={{ 'width': 57, 'height': 87 }} alt='' />
                    <h3 className='footer-title'>About Us</h3>
                    <hr />
                    <p className='footer-text'>Sed ac odio aliquet, fringilla odio eget, tincidunt nunc.
                        Duis aliquet pulvinar ante tempor tincidunt. Etiam lacus eros, viverra non massa tempus,
                        commodo fringilla nulla. Quisque vehicula.</p>
                </div>
                <div className='footer-content__element footer-content__map'>
                    <img className='footer-map' src={imgFooter2} alt='' />
                </div>
            </div>
            <div className='footer-social'>
                <div className='footer-social__items'>
                    {footer.map(i => {
                        return (
                            <div key={i.id} className='footer-social__items_item'>
                                <a href={i.link}>{i.icon}</a>
                            </div>
                        )
                    })
                    }
                </div>
            </div>
        </footer>
    )
}
export default Footer