import React from 'react'
import styled from 'styled-components'

const WhatWeDoItemStyle = styled.div`
    display: flex;
    flex-direction:row;
    /* width: 35%; */
    padding: 110px 20px;
    .tradition-assort {
        text-align: center;
        padding: 0 20px;

    }
`

const WhatWeDoItem = (props) => {
    console.log(props)
    return (
        <WhatWeDoItemStyle>
        {props.whatWeDoItem.map((item) => 
            <div className='tradition-assort'>
                <img className='tradition-assort-img' src={item.src} alt={item.title} />
                <div className='tradition-assort-body'>
                    <h3 className='tradition-assort-title'>{item.title}</h3>
                    <p className='tradition-assort-text'>{item.text}</p>
                </div>
            </div>
            )}
        </WhatWeDoItemStyle>
    )
}
export default WhatWeDoItem