import styled from "styled-components";
import Button from "../../common/button/Button";

const PricingPlansCardStyle = styled.div`
  width: 30%;
  background: #fff;
  margin: 0 30px;
  .pricing-card {
    text-align: center;
    padding: 30px;
  }
  .pricing-value {
    color: yellow;
    
  }
  .pricing-title {
    text-align: center;
    display: block;
    font-size: 34px;
    padding: 17px 0 32px;
    border-bottom: 1px solid #7d7d7d;
    box-sizing: border-box;
  }
 
  .pricing-usd {
    position: relative;
    bottom: 2px;
    font-size: 40px;
    line-height: 1em;
  }
  .pricing-price {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    font-size: 64px;
    line-height: 1em;
    left: -7px;
    padding: 22px 15px 0;
  }

  span {
    position: relative;
    display: block;
    line-height: 1em;
    margin: 2px 0 3px;
    font-size: 18px;
  }
`;

const ListStyle = styled.ul`
  font-size: 1rem;
  list-style-type: none;
`;
const PricingPlansCard = ({ title, pricingPlansListItem, price }) => {
  let listItem = pricingPlansListItem.map((i) => <li>{i}</li>);

  return (
    <PricingPlansCardStyle>
      <div className="pricing-card">
        <h3 className="pricing-title">{title}</h3>
        <div className="pricing-price">
          <div className="pricing-value">
            <sup className="pricing-usd">$</sup>
            {price}
            <span>per month</span>
          </div>
          <ListStyle>{listItem}</ListStyle>
        </div>
        <Button title="BUY" />
      </div>
    </PricingPlansCardStyle>
  );
};
export default PricingPlansCard;
