import React from "react";
import Button from "./../button/Button";
import styled, { keyframes } from "styled-components";
import { fadeInUpBig } from "react-animations";

const fadeInUpBigAnimation = keyframes`${fadeInUpBig}`;


const SliderContentStyle = styled.div`
  .slide-image {
    height: 100%;
    width: 100%;
  }
  .slide-body {
    width: 30%;
    position: absolute;
    top: 35%;
    right: 15%;
    text-align: center;
    color: black;
  }
  .slide-title {
    margin: 0px 0px 20px 0px;
    animation: 2.5s ${fadeInUpBigAnimation};
  }
  hr {
    animation: 2.75s ${fadeInUpBigAnimation};
  }
  .slide-text {
    margin: 30px 0;
    color: #7b7b7a;
    font-size: 19px;
    line-height: 26px;
    animation: 3s ${fadeInUpBigAnimation};
  }
.slide-btn {
  animation: 3.25s ${fadeInUpBigAnimation};
}
  .active {
    display: inline-block;
  }

  .inactive {
    display: none;
  }
`;


const SliderContent = (props) => {
  const sliderContent = props.slider.map((slide, index) => (
    <div
      key={index}
      className={index === props.activeIndex ? "slides active" : "inactive"}
    >
      <img src={slide.src} className="slide-image" alt={slide.title} />
      <div className="slide-body">
        <h1 className="slide-title">{slide.title}</h1>
        <hr />
        <p className="slide-text">{slide.text}</p>
        <div className="slide-btn">
          <Button title="Take a bite" />
        </div>
      </div>
    </div>
  ));
  return <SliderContentStyle>{sliderContent}</SliderContentStyle>;
};
export default SliderContent;
