import styled from "styled-components";

const SubTitleStyle = styled.div`

  margin: 0 auto;
  text-align: center;
  padding: 90px 0;
  width: 45%;
  hr {
    width: 75px;
  }
`;

const SubTitleTitleStyle = styled.h2`
  margin-bottom: 2rem;
`;

const SubTitleTextStyle = styled.p`
  color: #7c7c7c;
  font-size: 1.15rem;
  margin-top: 2rem;
`;

const SubTitle = (props) => {
  return (
    <SubTitleStyle {...props}>
      <SubTitleTitleStyle>{props.subTitleState.title}</SubTitleTitleStyle>
      <hr />
      <SubTitleTextStyle>{props.subTitleState.text}</SubTitleTextStyle>
    </SubTitleStyle>
  );
};
export default SubTitle;
