import { useState, useEffect } from "react";
import styled from "styled-components";

const CarouselStyle = styled.div`
  position: relative;
  margin: auto;
  overflow: hidden;
  .slide {
    padding-bottom: 150px;
    height: auto;
    width: 100%;
  }
   .slide-active {
    display: inline-block;
  }

  .slide-inactive {
    display: none;
  }
`;
const CarouselBodyStyle = styled.div`
  width: 50%;
  margin: 0 auto;
  text-align: center;
  line-height: 1.5rem;
`;
const CarouselTextStyle = styled.div`
  font-size: 17px;
`;
const CarouselAuthorStyle = styled.div`
  display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 25px;
`;

const Carousel = (props) => {
  const len = props.carouselItem.length - 1;
  const [activeIndex, setActiveIndex] = useState(0);

  // useEffect(() => {
  //   const interval = setInterval(() => {
  //     setActiveIndex(activeIndex === len ? 0 : activeIndex + 1)
  //   }, 3000)
  //   return () => clearInterval(interval)
  // }, [activeIndex])
  const carousel = props.carouselItem.map((slide, index) => (
    <div
      key={index}
      className={
        index === activeIndex ? "slide slide-active" : "slide-inactive"
      }
    >
      <CarouselBodyStyle>
        <CarouselTextStyle>{slide.text}</CarouselTextStyle>
        <CarouselAuthorStyle>
          <h3>{slide.author} /</h3>
          <h5>{slide.profession}</h5>
        </CarouselAuthorStyle>
      </CarouselBodyStyle>
    </div>
  ));
  return <CarouselStyle>{carousel}</CarouselStyle>;
};
export default Carousel;
