import React from 'react'
import styled from 'styled-components'
import Title from '../common/title/Title'
import Header from '../common/header/Header'
import Assortment from '../common/assortment/Assortment'
import OdometerAssortment from './OdometerAssortment/OdometerAssortment'
import { ThemeContext } from './../../App'
import aboutUsData from './aboutUsData'
import { titleState } from '../common/title/titleState'

import assortmentImage from './../../assets/about-us-img-1.png'

const AboutUsStyle = styled.div`
  
`
const AboutUsPage = (props) => {

  console.log(props)
  return (
    <AboutUsStyle {...props}>
      <ThemeContext.Consumer>
        {themeColors => <Header value={themeColors.gold} />}
      </ThemeContext.Consumer>

      <Title light title={titleState[0]} />
      <Assortment assortmentItem={aboutUsData.aboutUsContent} img={assortmentImage} />
      <OdometerAssortment odometer={aboutUsData.odometer} />
    </AboutUsStyle>
  )
}
export default AboutUsPage
