import SubTitle from '../../common/subTitle/SubTitle'
import Carousel from '../../common/carousel/Carousel'
import styled from 'styled-components'
import amazingBg from './../../../assets/h1-background-img-2.jpg'

const  AmazingStyle = styled.div`
    background: no-repeat url(${amazingBg});
`

const AmazingTaste = (props) => {
    return (
        <AmazingStyle> 
            <SubTitle subTitleState={props.subTitleState[1]}/>
            <Carousel carouselItem={props.carouselItem} />
        </AmazingStyle>
    )
}
export default AmazingTaste 

