import AssortmentElement from "./AssortmentElement";
import styled, { keyframes } from "styled-components";
import { fadeInRight, fadeInLeft } from "react-animations";

import assortmentBg from "./../../../assets/h1-parallax-1.jpg";
import assortmentImg from "./../../../assets/h1-img-5.png";

const fadeInRightAnimation = keyframes`${fadeInRight}`;
const fadeInLeftAnimation = keyframes`${fadeInLeft}`;

const AssortmentStyle = styled.div`
  background: url(${assortmentBg}) fixed;
  .wrapper {
    width: 70%;
    margin: 0 auto;
    display: flex;
    padding: 90px 0;
    color: #fff;
  }

  .assortment-img {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
`;
const AssortItemLeftStyle = styled.div`
  animation: 2s ${fadeInLeftAnimation};
  text-align: end;
`;
const AssortItemRightStyle = styled.div`
  animation: 2s ${fadeInRightAnimation};
  text-align: start;
`;

const Assortment = (props) => {
  let state = props.assortmentItem;
  let leftElement = state
    .slice(0, 3)
    .map((i) => <AssortmentElement title={i.title} text={i.text} key={i.id} />);

  let rightElement = state
    .slice(3, 6)
    .map((i) => <AssortmentElement title={i.title} text={i.text} key={i.id} />);

  return (
    <AssortmentStyle {...props}>
      <div className="wrapper">
        <AssortItemLeftStyle>{leftElement}</AssortItemLeftStyle>
        <div className="assortment-img">
          <img src={assortmentImg} alt="Cookie" />
        </div>
        <AssortItemRightStyle>{rightElement}</AssortItemRightStyle>
      </div>
    </AssortmentStyle>
  );
};
export default Assortment;
