import React from 'react'
import { FaAngleLeft, FaAngleRight } from 'react-icons/fa'
import styled from 'styled-components'

const ArrowsStyle = styled.div`
        .left-arrow, .right-arrow{
            cursor: pointer;
            z-index: 99;
            top: 50%;
            position: absolute;
            font-size: 50px;
        }
        
        .left-arrow {
        left: 50px;
        }
        
        .right-arrow {
            right: 50px;
        }
        
        .left-arrow:hover, 
        .right-arrow:hover {
            color: #c9a74d;
            transition: all 0.1s ease-in;
        }

        /* @media (max-width : 769px) {
            .right-arrow, .left-arrow {
                visibility: hidden;
        }
        } */
    `

const Arrows = (props) => {
        
    return (
        <ArrowsStyle>
            <FaAngleLeft className='left-arrow' onClick={props.prevSlide} />
            <FaAngleRight className='right-arrow' onClick={props.nextSlide} />
        </ArrowsStyle>
    )
}

export default Arrows
