import React from 'react'
import styled from 'styled-components'
import Button from '../common/button/Button'
import Header from '../common/header/Header'

import { ThemeContext } from './../../App'

const CartContentStyle = styled.div`
 
`

const CartTitleStyle = styled.h1`
  padding: 7%;
  background-color: #fafafa;
`

const CartTextStyle = styled.p`
  font-size: 1.5rem;
  padding: 40px 0;
  margin: 0 auto;
  text-align: center;
`

const CartPage = (props) => {
  return (
    <>
      <ThemeContext.Consumer>
        {themeColors => <Header value={themeColors.dark} navItems={props.navItems} />}
      </ThemeContext.Consumer>
      <CartContentStyle>
        <CartTitleStyle>CART</CartTitleStyle>
        <CartTextStyle>Your cart is currently empty.</CartTextStyle>
        <Button title='Return to shop' />
      </CartContentStyle>
    </>
  )
}
export default CartPage
