import React from 'react'
import styled from 'styled-components'
import { FaVimeo, FaInstagram, FaTwitter, FaFacebookF } from 'react-icons/fa';

const TeamContactStyle = styled.div`
    font-size: 18px;
    .team-list {
        list-style: none;
        display: flex;
        justify-content: center;
    }
    li {
        padding: 10px 5px;
        }
    li>a {
        color: #c9a74d;  
        }
`

const TeamContact = (props) => {

    return (
        <TeamContactStyle>
            <ul className='team-list'>
                <li><a href={props.contactVimeo}><FaVimeo /></a></li>
                <li><a href={props.contactInsta}><FaInstagram /></a></li>
                <li><a href={props.contactTwitter}><FaTwitter /></a></li>
                <li><a href={props.contactFB}><FaFacebookF /></a></li>
            </ul>
        </TeamContactStyle>
    )
}
export default TeamContact