import React from 'react'
import styled from 'styled-components'
import TeamContact from './TeamContacts';

const TeamCardStyle = styled.div`
.team-card {
    margin: 50px;
    text-align: center;
}

.team-photo {
    width: 100%;
    margin: 15px;
}

.team-description {
    padding: 0 15px;
}

.team-position {
    color: #c9a74d;
}

.team-contacts {
    margin: 0 auto;
}
`;

const TeamCard = (props) => {
    console.log(props)
    return (
        <TeamCardStyle>
            <div className='team-card'>
                <img className='team-photo' src={props.photo} alt={props.name}/>
                <div className='team-description'>
                    <h3 className='team-name'>{props.name}</h3>
                    <span className='team-position'>{props.position}</span>
                    <p>{props.description}</p>
                </div>
                <TeamContact className='team-contacts' contactVimeo={props.contactVimeo}
                    contactInsta={props.contactInsta}
                    contactTwitter={props.contactTwitter}
                    contactFB={props.contactFB} />
            </div>
        </TeamCardStyle>
    )
}
export default TeamCard