import LineProgressBar from '../common/progressBar/LineProgressBar';
import { ThemeContext } from './../../App';
import React, {useState} from 'react';
import styled from 'styled-components';
import Header from '../common/header/Header';
import Title from '../common/title/Title';
import { titleState } from '../common/title/titleState';
import CircleProgressBar from '../common/progressBar/CircleProgressBar';
const tes = [
  { bgcolor: "#c9a74d", completed: 68 },
  { bgcolor: "#c9a74d", completed: 85 },
  
];

const OurProcessStyle = styled.div`

`;

const OurProcessPage = (props) => {
  const [progress, setProgress] = useState(0);
  
console.log(props) 
  return (
    <OurProcessStyle>
      <ThemeContext.Consumer> 
        {themeColors => <Header value={themeColors.dark} navItems={props.navItems}/> } 
      </ThemeContext.Consumer>
      <Title dark title={titleState[3]} />
      <div className="z">
        {tes.map((item, i) => (
          <LineProgressBar key={i} bgcolor={item.bgcolor} completed={item.completed} />
        ))}
      </div>
      <CircleProgressBar progress={14}
          size={172}
          strokeWidth={4}
          circleOneStroke='#c9a74d'
          circleTwoStroke='#fff' />
      <CircleProgressBar progress={75}
          size={172}
          strokeWidth={4}
          circleOneStroke='#c9a74d'
          circleTwoStroke='#fff' />
    </OurProcessStyle>

  )
}
export default OurProcessPage