import styled from 'styled-components'

const WhatWeDoElemStyle = styled.div`
    display: flex;
    flex-direction:row;
    padding: 110px 20px;
    .tradition-assort {
        text-align: center;
        padding: 0 20px;
        background: ${(props) => props.background};
    }
`

const WhatWeDoElem = (props) => {

    return (
        <WhatWeDoElemStyle {...props}>
            {props.whatWeDoElem.map((i) =>
                <div className='tradition-assort' background={props.background}>
                    <img className='tradition-assort-img' src={i.src} alt={i.title} />
                    <div className='tradition-assort-body'>
                        <h3 className='tradition-assort-title'>{i.title}</h3>
                        <p className='tradition-assort-text'>{i.text}</p>
                    </div>
                </div>
            )}
        </WhatWeDoElemStyle>
    )
}
export default WhatWeDoElem