import React from 'react'
import './App.css'
import { HashRouter, Switch, Route } from 'react-router-dom'

import HomePage from './components/HomePage/HomePage'
import AboutUsPage from './components/AboutUsPage/AboutUsPage'
import WhatWeDoPage from './components/WhatWeDoPage/WhatWeDoPage'
import OurTeamPage from './components/OurTeamPage/OurTeamPage'
import OurProcessPage from './components/OurProcessPage/OurProcessPage'
import PricingPlansPage from './components/PricingPlansPage/PricingPlansPage'
import ContactUsPage from './components/ContactUsPage/ContactUsPage'
import BlogPage from './components/BlogPage/BlogPage'
import ShopPage from './components/ShopPage/ShopPage'
import SearchPage from './components/SearchPage/SearchPage'
import CartPage from './components/CartPage/CartPage' 
import Footer from './components/common/footer/Footer'

import darkLogo from './assets/logo-dark-2.png'
import goldLogo from './assets/light-logo.png'
import whiteLogo from './assets/logo-white.png'

const themeColors = {
  light: {
    color: '#fff',
    logo: whiteLogo,
    backgroungColor: '#fff'
  },
  dark: {
    color: '#030303',
    logo: darkLogo,
    backgroungColor: '#030303'
  },
  gold: {
    color: '#c9a74d',
    logo: goldLogo,
    backgroungColor: '#c9a74d'
  }
}

// const themeMedia = {
//   media: {
//     phone: '(max-width: 425px)',
//     tablet: 'screen and (max-width: 768px)',
//     pc: '(min-width: 768px)'
//   }
// }

export const ThemeContext = React.createContext(themeColors.light)

const App = (props) => {
  console.log(props)
  return (
    <HashRouter>
      <div className="app-wrapper">
        <Switch>
          <ThemeContext.Provider value={themeColors}>
            <Route exact path='/'
              render={() => <HomePage />} />
            <Route path='/about-us'
              render={() => <AboutUsPage />} />
            <Route path='/what-we-do'
              render={() => <WhatWeDoPage />} />
            <Route path='/our-team'
              render={() => <OurTeamPage />} />
            <Route path='/our-process'
              render={() => <OurProcessPage />} />
            <Route path='/pricing-plans'
              render={() => <PricingPlansPage />} />
            <Route path='/contact-us'
              render={() => <ContactUsPage />} />
            <Route path='/blog'
              render={() => <BlogPage />} />
            <Route path='/shop'
              render={() => <ShopPage />} />
            <Route path='/search'
              render={() => <SearchPage />} />
            <Route path='/cart'
              render={() => <CartPage />} />
          </ThemeContext.Provider>
        </Switch>
        <Footer />
      </div>
    </HashRouter>
  )
}

export default App
