import React from 'react'
import leftFlavor from './../../../assets/h1-img-20.png'
import styled from 'styled-components'
import SubTitle from '../../common/subTitle/SubTitle'
import Button from '../../common/button/Button'

const FlavorStyle = styled.div`
    display: flex;
    background-color: #fdfbf6;
    .flavor-img {
        width: 40%;
        position: relative;
        top: -90px;
        z-index: 999;
    }
`

const Flavor = (props) => {
    return (
        <FlavorStyle>
            <img className='flavor-img' src={leftFlavor} alt='slide-img' />
            <div className='flavor-body'>
                <SubTitle subTitleState={props.subTitleState[2]} />
                <Button title='Order' />
            </div>
        </FlavorStyle>
    )
}
export default Flavor