import styled from 'styled-components'
import React, { useState, useEffect } from 'react'

// import OdometerAssortmentItem from './OdometerAssortmentItem'
import Odometer from 'react-odometerjs'
import "./styles.css"
import odometerBg from './../../../assets/about-us-parallax-1.jpg'

const OdometerAssortmentStyle = styled.div`
    background: no-repeat url(${odometerBg});
    width: 100%;
    display: flex;
`
const OdometerStyle = styled.div`
    width: 80%;
    padding: 50px;
    margin: 0 auto;
    color: white;
    justify-content: space-between;
`
const OdometerAssortment = (props) => {
    const [odometerValue, setOdometerValue] = useState(0)

    useEffect(() => {
        setOdometerValue(props.odometer.map(i => i.counter))
    }, [])
    

    return (

        <OdometerAssortmentStyle>
            {props.odometer.map(i => (
                <OdometerStyle>
                    <img src={i.src} />
                    <Odometer value={i.counter} format="(d)" />
                    <div>{i.text}</div>
                </OdometerStyle>
            )
            )}
        </OdometerAssortmentStyle>
    )
}
export default OdometerAssortment