import React from "react";
import TraditionItem from "./TraditionItem";
import SubTitle from "../../common/subTitle/SubTitle";
import styled from "styled-components";

import traditionBg from './../../../assets/h1-background-img-1.jpg'

const TraditionStyle = styled.div`
  background: url(${traditionBg});
  width: 100%;

  /* @media (max-width : 769px) {
    .tradition { 
      display: grid;
      grid-template-columns: repeat(3, 1fr);
    }
  } */
`;

const TraditionItemsStyle = styled.div`
  width: 80%;
  margin: 0 auto;
  display: flex;
`;
const Tradition = (props) => {
  return (
    <TraditionStyle {...props}>
      <SubTitle subTitleState={props.subTitleState[0]} />
      <TraditionItemsStyle>
        {props.traditionItem.map((i) => (
          <TraditionItem key={i.id} title={i.title} text={i.text} src={i.src} />
        ))}
      </TraditionItemsStyle>
    </TraditionStyle>
  );
};
export default Tradition;
