import React from 'react'
import styled from 'styled-components'

const TraditionItemStyle = styled.div`
    /* display: flex; */
    width: 35%;
    padding: 110px 20px;
    .tradition-assort {
        text-align: center;
        padding: 0 20px;
    }
`

const TraditionItem = (props) => {
    return (
        <TraditionItemStyle>
            <div className='tradition-assort'>
                <img className='tradition-assort-img' src={props.src} alt={props.title} />
                <div className='tradition-assort-body'>
                    <h3 className='tradition-assort-title'>{props.title}</h3>
                    <p className='tradition-assort-text'>{props.text}</p>
                </div>
            </div>
        </TraditionItemStyle>
    )
}

export default TraditionItem
