import styled from "styled-components";
import PricingPlansCard from "./PricingPlansCard";

const PricingPlansCardStyle = styled.div`
  display: flex;
  padding: 30px 10px;
  justify-content: center;
  margin: 0 30px;
  
`;

const PricingPlansCards = (props) => {
  let card = props.pricingPlansPricesCards.map(i => (
    <PricingPlansCard
      key={i.id}
      title={i.title}
      price={i.price}
      pricingPlansListItem={i.pricingPlansListItem}
    />
  ));
  console.log(card);
  return (
    <PricingPlansCardStyle>
      {card}
    </PricingPlansCardStyle>
)};
export default PricingPlansCards;
