import { ThemeContext } from "./../../App";
import styled from "styled-components";
import Header from "../common/header/Header";
import Title from "../common/title/Title";
import { titleState } from "../common/title/titleState";
import SubTitle from "../common/subTitle/SubTitle";
import { subTitleState } from "../common/subTitle/subTitleState";
import { pricingPlansState } from "./pricingPlansState";
import Tradition from "../HomePage/tradition/Tradition";

import greatPricesBg from "./../../assets/services-background-img-12.jpg";
import PricingPlansPricesCards from "./pricingPlansPrices/PricingPlansCards";
import traditionBg from './../../assets/pricing-parallax.jpg'

const PricingPlansStyle = styled.div`
  background: url(${greatPricesBg});
  position: relative;
    top: -90px;
  .app-container {
    
  }
`;

const PricingPlansPage = (props) => {
  console.log(props);
  return (
    <>
      <ThemeContext.Consumer>
        {(themeColors) => <Header value={themeColors.gold} />}
      </ThemeContext.Consumer>
      <Title dark title={titleState[4]} />
      <PricingPlansStyle>
      <div className="app-container">
        <SubTitle subTitleState={subTitleState[11]} />
        <PricingPlansPricesCards
          pricingPlansPricesCards={pricingPlansState.pricingPlansPricesCards}
        />
      </div>
      <Tradition traditionItem={pricingPlansState.pricingPlansAssortment}
        subTitleState={subTitleState}
        background={traditionBg} />
    </PricingPlansStyle></>
  );
};

export default PricingPlansPage;
