import React from 'react'
import styled from 'styled-components'
import { FaCircle } from 'react-icons/fa'

const DotStyle = styled.div`
    width: 100%;
    position: absolute;
    display: flex;
    bottom: 90px;
    justify-content: center;
    z-index: 200; 
    
            
    .dot {
        cursor: pointer;  
        width: 1rem;
        height: 1rem;
        margin: 0px 3px;
        position: absolute;
        top: 0;
        border-radius: 50%;
        display: flex;
        color: green;
    }
    
    .dot-active, .dot:hover {
        color: #c9a74d;
    }
    .dot-inactive {
        color: red;
    }
    
    
    /* @media (min-width: 769px) {
        visibility: hidden; 
    }*/  
`

const Dots = (props) => {
    console.log(props)
    return (
        <DotStyle>
            {props.slider.map((dot, index) =>
                <div key={dot.id} className={`${props.activeIndex === index ? 'dot dot-active' : 'dot-inactive'}`}>
                      <FaCircle onClick={dot.onClick}/>  
                </div>
            )}
            
        </DotStyle>
    )
}

export default Dots
