import React from 'react';
import SubTitle from './../../common/subTitle/SubTitle'

import styled from 'styled-components'
import TeamCard from './TeamCard'

const TeamStyle = styled.div`
        position: relative;
        top: -90px;

     .wrapper {
        display: flex; 
        width: 80%;  
        margin: 0 auto;
    }   
    `;
const Team = (props) => {
    let state = props.teamItem
    let teamCard = state
    .slice(0, 4)
    .map(i => <TeamCard 
        key={i.id}
        photo={i.photo}
        name={i.name}
        description={i.description}
        position={i.position}
        contactVimeo={i.contactVimeo}
        contactInsta={i.contactInsta}
        contactTwitter={i.contactTwitter}
        contactFB={i.contactFB}
    />)

    return (
        <TeamStyle>
            <SubTitle subTitleState={props.subTitleState[5]} />
            <div className='wrapper'>{teamCard}</div>
        </TeamStyle>
    )
}
export default Team
